<template>
  <div>
    <el-header>{{title}}</el-header>
    <div id="pieChart"></div>
  </div>
</template>
<script>
export default {
  name: 'pieChart',
  data() {
    return {
      title: ''
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      this.chartInit()
    },
    chartInit() {
      let myChart = this.$echarts.init(document.getElementById('pieChart'))
      let options = {
        title: {
          text: '',
          x: 'center',
          textStyle: {
            color: '#ffffff'
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 20,
          data: ['', '', '', '', ''],
          textStyle: {
            color: '#ffffff'
          }
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: ['35%','70%'],
            center: ['50%', '60%'],
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              formatter: '{c|{c}} {b|} \n {a|}',
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '18',
                  fontWeight: 'bold'
                }
              }
            },
            data: [
              { value: 335, name: '' },
              { value: 310, name: '' },
              { value: 234, name: '' },
              { value: 135, name: '' },
              { value: 1548, name: '' }
            ],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
      myChart.setOption(options)
      this.autoActive(myChart, options)
    },
    autoActive(chart, option) {
      this.intervalFun(chart, option)
      // let that = this
      // document.getElementById('pieChart').on('mouseover',function(){
      //   clearInterval(that.interval)
      //   let dataLen = option.series[0].data.length
      //   for(let i = 0; i < dataLen; i++){
      //     chart.dispatchAction({
      //       type: 'downplay',
      //       seriesIndex: 0,
      //       i
      //     })
      //   }
      // })
      // chart.on('mouseout',function(){
      //   that.intervalFun(chart, option)
      // })
    },
    intervalFun(chart, option) {
      let dataIndex = -1
      let dataLen = option.series[0].data.length
      this.interval = setInterval(() => {
        chart.dispatchAction({
          type: 'downplay',
          seriesIndex: 0,
          dataIndex
        })
        dataIndex = (dataIndex + 1) % dataLen
        chart.dispatchAction({
          type: 'highlight',
          seriesIndex: 0,
          dataIndex
        })
        //  tooltip
        // chart.dispatchAction({
        //   type: 'showTip',
        //   seriesIndex: 0,
        //   dataIndex
        // })
      }, 1500)
    }
  }
}
</script>
<style lang="scss" scoped>
#pieChart{
  width: 100%;
  height: 360px;
}
</style>

