<template>
  <div>
    <div id="linesChart"></div>
  </div>
</template>
<script>
export default {
  name: 'linesChart',
  data() {
    return {
      title: '',
      xRows: [],
      allData: [],
      lineTitle: [],
      colorArr: ['#9eb520','#845678','#756898','#912234','#609090'],
      responseData: {
        code: 0,
        data: {
          title: '',
          x: ['5.13','5.14','5.15','5.16','5.17'],
          list: [
            {
              name: '1',
              data: [100,200,300,400,'-'],
              smooth: false,
              lintType: 'solid'
            },
            {
              name: '1',
              data: ['-','-','-',400,500],
              smooth: false,
              lintType: 'dotted'
            },
            {
              name: '2',
              data: [1800,550,740,350,'-'],
              smooth: false,
              lintType: 'solid'
            },
            {
              name: '2',
              data: ['-','-','-',350,600],
              smooth: false,
              lintType: 'dotted'
            },
            {
              name: '3',
              data: [180,550,310,60,'-'],
              smooth: false,
              lintType: 'solid'
            },
            {
              name: '3',
              data: ['-','-','-',60,530],
              smooth: false,
              lintType: 'dotted'
            }
          ]
        }
      }
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      this.title = this.responseData.data.title
      this.xRows = this.responseData.data.x
      for(let i = 0; i < this.responseData.data.list.length; i ++){
        this.lineTitle.push(this.responseData.data.list[i].name)
        let singleData = {
          name: this.responseData.data.list[i].name,
          type:'line',
          data: this.responseData.data.list[i].data,
          smooth: this.responseData.data.list[i].smooth,
          lineStyle: {
            color: this.colorArr[i]
          },
          itemStyle:{
            normal:{
              lineStyle:{
                color: this.colorArr[i],
                width:2,
                type:this.responseData.data.list[i].lintType  //'dotted' 'solid'
              }
            }
          }
        }
        this.allData.push(singleData)
      }
      this.linesInit()
    },
    linesInit(){
      let myChart = this.$echarts.init(document.getElementById('linesChart'))
      // 
      myChart.setOption({
        title: {
          text: this.title,
          textStyle: {
            color: '#00ce7d'
          },
          top: 20
        },
        // 
        tooltip: {
          trigger: 'axis',
          formatter: function (params) {
            var htmlStr = '<div style="text-align:center">';
            var valMap = {};
            for(var i=0;i<params.length;i++){
              var param = params[i];
              var xName = param.name;//x
              var seriesName = param.seriesName;//
              var value = param.value;//y
              var color = param.color;//
              //
              if(value == '-'){
                continue;
              }
              //
              if(valMap[seriesName] == value){
                continue;
              }
              if(i===0){  
                htmlStr += xName + '<br/>';//x  
              }  
              htmlStr +='<div>';  
              //  
              htmlStr += '<span style="margin-right:5px;display:inline-block;width:10px;height:10px;border-radius:5px;background-color:'+color+';"></span>';  
              //  
              htmlStr += seriesName + '' + value;
              htmlStr += '</div>';
              valMap[seriesName] = value;
            }
            htmlStr += '</div>';
            return htmlStr;  
          }
        },
        legend: {
          data: this.lineTitle,
          textStyle: {
            color: '#00ce7d'
          },
          right: 0,
          top: 25
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        // 
        // toolbox: {
        //   feature: {
        //     saveAsImage: {}
        //   }
        // },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.xRows,
          axisLabel: {
            show: true,
            textStyle: {
              color: '#00ce7d'
            }
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#00ce7d'
            }
          }
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            show: true,
            textStyle: {
              color: '#00ce7d'
            }
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#00ce7d'
            }
          }
        },
        series: this.allData
      });
    }
  }
}
</script>
<style lang="scss" scoped>
#linesChart{
  width: 100%;
  height: 240px;
  padding: 0 20px;
}
</style>
